* {
    box-sizing: border-box;
}

@font-face {
    font-family: mainFont;
    src: url();
}

.m-350 {
    max-width: 350px;
}

.Login-Box {
    border-color: black;
    border-width: 1px;
    border-style: solid;
    border-radius: 2px;
    background-color: white;
    line-height: 18px;
    margin-top: 10px;
    /* max-width: 350px; */
    height: 22em;
    position: relative;
    vertical-align: baseline;
    padding: 60px 0;
    top: 70px;
    left: 50%;
}

.Bottom-Box {
    max-width: 350px;
    line-height: 1;
    text-align: center;
    max-height: 1px;
}

.app-name {
    margin: 0;
    text-align: center;
    font-family: 'Dancing Script', cursive;
}

.login-input {
    margin-left: 10%;
    margin-top: 5%;
    margin-right: 10%;
    width: 80%;
    background-color: whitesmoke;
    height: 35px;
    padding-left: 10px;
}
.no-border {
    border-style: none;
}
.thin-border {
    border-width: thin;
}
.button {
    position: relative;
    margin-left: 10%;
    width: 80%;
    margin-top: 5%;
    background-color: rgba(0, 149, 256, 1);
    height: 30px;
    color: white;
    padding: 0;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    border-style: none;
    border-radius: 20px;
    cursor: pointer;
}

@media all and (max-width: 600px) {
    .Login-Box {
        position: relative;
        left: 8%;
    }
}

.loading-screen-container {
    position: absolute;
    border-style: solid;
    border-width: thin;
    width: auto;
    height: auto;
    min-width: 300px;
    min-height: 100px;
    /* border-color: brown; */
    border-radius: 0.5em;
    z-index: 10;
    top: 40%;
    /* padding: 2px; */
    resize: both;
    background-color: white;
    left: 40%;
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
    transition: all 0.3s ease-in-out;
    /* transform: translate(-50%, -50%); */
}

.modal-show {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.d-flex-vertical {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* align-items: center; */
}

.d-flex-horizontal {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    /* align-items: center; */
}

.vertical-flex-container {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: inherit;
}

.m-10 {
    /* margin-top: 2%; */
}
.modal-footer {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
    z-index: 5;
}

.hidden {
    display: none;
}

.modal-header {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-bottom-style: solid;
    /* border-bottom: thick; */
    border-color: black;
    border-width: 100%;
    color: white;
    background-color: rgba(0, 0, 255, 0.6);
    width: inherit;
    font-weight: bold;
}

.form-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-color: black;
    border-width: 1px;
    border-style: solid;
    border-radius: 2px;
    background-color: white;
    line-height: 18px;
    margin-top: 10px;
    position: relative;
    left: 40%;
    vertical-align: center;
    padding: 30px 0;
}
hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #000;
    margin: 1em 0;
    padding: 0;
}
